<template>
  <div id="works">
    <div id="nav">
      <span id="title">正在施工</span>

    </div>
    <div id="container" >
      <div class="Router" ref="container">
        <transition name="slide-left" >

          <router-view/>

        </transition>
      </div>

    </div>
  </div>
</template>

<style>
#works{
  position: relative;
  height:100%;
  margin:0 auto;
  margin-top:-500px;
  background-color: rgb(255,255,255);

  z-index:99;
}
  #nav {
    padding-left: 30px;
    text-align: center;
    background-image:linear-gradient(135deg, rgb(67, 203, 255) 10%, rgb(70, 53, 217) 100%);
    color:white;
    height:3em;
    line-height:3em;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.82);
    position:relative;
  }
  #nav #title{
    font-size:20px;
  }
  #nav a {
    font-weight: bold;
    color: rgba(44, 62, 80, 0.9);
    padding:20px;
  }

  #nav a.router-link-exact-active {
    color: rgba(255, 255, 255, 0.95);
  }

  #container{
    width:70%;
    margin:0 auto;
    text-indent: 2em;
    height:100%;
    overflow: auto;
  }
  #container h1{
    background-color: rgba(108, 199, 234, 0.78);
  }

  .Router{
    position: relative;
    height:100%;
    z-index:-1;
  }
  .Router>*{
    position: absolute;
    width: 100%;
    height:100%;

    /*text-align: center;*/
  }
  .slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
    will-change: transform;
    transition: all 500ms;
  }

  .slide-right-enter {
    opacity: 0;
    transform: translateX(-100%);
  }

  .slide-right-leave-active {
    opacity: 0;
    transform: translateX(100%);
  }

  .slide-left-enter {
    opacity: 0;
    transform: translateX(100%);
  }

  .slide-left-leave-active {
    opacity: 0;
    transform: translateX(-100%);
  }
</style>
<script>
export default {
  name: 'Works',
  comments: true,
  data () {
    return {

      winHeight: window.innerHeight - 50

    }
  },

  mounted () {
    // 挂载浏览器高度获取方法

    const that = this

    window.onresize = () => {
      return (() => {
        that.winHeight = window.innerHeight - 50
      })()
    }
    // console.log(that.winHeight)
    this.$refs.container.style.height = this.winHeight + 'px'
  },

  watch: {

    // 监控浏览器高度变化

    winHeight (val) {
      this.winHeight = val

      this.$refs.container.style.height = this.winHeight + 'px'
    }

  }
}
</script>
